body {
    overflow: hidden; 
}

.App {
    font-family: "Raleway", sans-serif;
    text-transform: uppercase;
    background: #f5f5f5;
    display: -ms-flexbox;
    display: flex;
}

.Navbar {
    background: #3498db;
    padding: .5rem 2rem;
    color: #fff;
    text-align: center;
    overflow-y: auto;
    -webkit-box-shadow: 2px 3px 16px rgba(0, 0, 0, 0.35);
            box-shadow: 2px 3px 16px rgba(0, 0, 0, 0.35);
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-user-drag: none;
}

.Navbar .title {
    font-size: 3rem;
    text-shadow: 2px 3.464px 16px rgba(0, 0, 0, 0.35);
    cursor: default;
}

.Navbar .date-link {
    font-size: 2rem;
    display: block;
    margin-top: 1rem;
    cursor: pointer;
    color: #fff;
    text-decoration: none;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
}

.Navbar .date-link:hover {
    text-shadow: 1px 6px 16px rgba(0,0,0,.35);
}

.Navbar .date-link.disabled {
  opacity: .6;
  cursor: default;
}

.Navbar .date-link.disabled:hover {
  text-shadow: none;
}

.Main {
    padding: 1rem 4rem;
    overflow-y: auto;
    overflow-x: hidden;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    max-height: 100vh;
    -ms-flex: 1 1 0%;
        flex: 1 1 0%;
    z-index: 0;
    height: 100vh;
}

.Main .ClearButtons {
    float: right;
    margin-top: .5rem;
}

.Main .ClearButtons .btn {
    display: inline-block;
    background: #3498db;
    padding: .4rem .8rem;
    border-radius: 4px;
    color: #fff;
    font-weight: 600;
    -webkit-transition: all .15s;
    -o-transition: all .15s;
    transition: all .15s;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-user-drag: none;
    cursor: pointer;
}

.Main .ClearButtons .btn:hover {
    background: #47a4e2;
}

.Main .ClearButtons .btn.disabled {
    opacity: .6;
    cursor: default;
}

.Main .ClearButtons .btn.disabled:hover {
    background: #3498db;
}

.Main .ClearButtons .clear-history.btn {
    margin-right: 1rem;
}

.Main .no-history {
    position: relative;
    float: left;
    top: 10rem;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    color: #5dafe6;
    font-size: 2.5rem;
    cursor: default;
}

.Main .DateDiv .date {
    color: #5dafe6;
    font-size: 2.5rem;
    cursor: default;
    margin: 0;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-user-drag: none;
}

.Main .DateDiv:first-child .date {
    margin-top: 2rem;
}

.Main .DateDiv .sites {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    padding: 2rem 0;
}

.Main .DateDiv .sites .Site {
    display: inline-block;
    margin: .5rem 4rem 3.5rem 0;
    position: relative;
    cursor: pointer;
    text-align: center;
    color: #43a4e6;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-user-drag: none;
}

.Main .DateDiv .sites .Site:last-child {
    margin-right: 0;
}

.Main .DateDiv .sites .Site .circle {
    background: #fff;
    border-radius: 50%;
    height: 10rem;
    width: 10rem;
    display: inline-block;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
    border: 1px solid #c3c3c3;
    background-position: 50%;
    background-size: 100% 60%;
    background-repeat: no-repeat;
    position: relative;
    overflow: hidden;
}

.Main .DateDiv .sites .Site:hover .circle {
    -webkit-box-shadow: -3px 4px 20px #122b3b;
            box-shadow: -3px 4px 20px #122b3b;
    border: 1px solid transparent;
    border-radius: 2rem;
    background-size: 150% 90%;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-user-drag: none;
}

.Main .DateDiv .sites .Site .circle .title {
    max-height: 3rem;
    overflow: hidden;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-user-drag: none;
}

.Main .DateDiv .sites .Site:hover .circle .title {
    top: -1rem;
}

.Main .DateDiv .sites .Site .circle .time {
    overflow: hidden;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-user-drag: none;
}

.Main .DateDiv .sites .Site:hover .circle .time {
    bottom: -.5rem;
}

.Main .DateDiv .sites .Site .info {
    display: block;
    width: 10rem;
    height: 0;
    background: #4ca9e7;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
    overflow: hidden;
    z-index: -5;
    position: absolute;
    top: 4rem;
}

.Main .DateDiv .sites .Site:hover .info {
    top: 8rem;
    height: auto;
    border-radius: 0 0 2rem 2rem;
    padding: .2rem 0 .1rem;
}

.Main .DateDiv .sites .Site .info .domain {
    color: #fff;
    opacity: 0;
    margin-top: 2.5rem;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
    overflow: hidden;
    border: 1px solid transparent;
    border-width: 0 .6rem;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}

.Main .DateDiv .sites .Site:hover .info .domain {
    opacity: 1;
}


